
.popup {
    position: fixed;
    top: 40px;
    left: 7vw;
    width: 90vw;
    height: 200vh;
    background-color: rgba(255,255,255,0.8);
    border-radius: 60px;
    border: solid 2px #E08330;
    display : flex;
    justify-content : center;
    overflow:scroll;
}



.popup-inner .close-btn {
    position: absolute;
    top: 16px;
    right: 16px;

}

